import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/fontawesome'
import './plugins/bootstrap-vue'
import App from './App.vue'
import {library} from "@fortawesome/fontawesome-svg-core";
import {faExternalLinkAlt, faTimesCircle} from '@fortawesome/free-solid-svg-icons';
import {faGithubSquare, faLinkedin, faLastfmSquare, faGithub} from '@fortawesome/free-brands-svg-icons';
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";

library.add(faGithubSquare, faLinkedin, faLastfmSquare, faGithub, faExternalLinkAlt, faTimesCircle);

Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false;

new Vue({
  render: h => h(App),
}).$mount('#app');
